import Vue from 'vue';
import VueRouter from 'vue-router';
import Signup from '../components/signup.vue';




Vue.use(VueRouter)
const routes =[
    {path:'/signup', component:Signup}
]


const router = new VueRouter({
    mode:'history',
    base:process.env.BASE_URL,
    routes
    
})
export default router;